body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 97vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.jumbotron {
  margin-bottom: 0;
}

.team-comparison {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.team-container {
  width: 50%;
  min-width: 400px;
  display: -webkit-flex;
  display: flex;
}

.team-dashboard {
  display: grid;
  grid-template-columns: 25fr 25fr 25fr 25fr;
  grid-template-rows: 33fr 33fr 33fr;
  height: 97vh;
}

.split-view-stats {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.split-view-stat {
  width: 50%;
  min-width: 220px;
}

.points-text {
  margin-top: 20%;
}

.team-content-container {
  width: 100%;
  max-width: 700px;
  padding: 20px 5px 0px 5px;
}

@media only screen and (max-width: 600px) {
  .team-content-container {
    width: 100%;
    padding: 10px 0 0 50;
  }
}


.charts-and-trends-container {
  width: 100%;
  padding: 0 0 10px 0;
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.welcome-header-container {
  padding-top: 30vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.welcome-header {
  background-color: #eeeeee;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 30px 390px 10px 390px;
}

@media only screen and (max-width: 600px) {
  .welcome-header-container {
    padding-top: 10vh;
  }
  .welcome-header {
    width: 100%;
    padding: 30px 0 10px 0;
  }
}

h1 {
  font-family: 'Aldrich';
  font-style: italic;
}

.team-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 20px;
  color: #222222;
  background-color: #ffffff;
  border-bottom: 1px solid #555555;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.stat-container {
  width: 100%;
  margin-bottom: 10px;
  background-color: white;
  padding: 10px;
}

.comparison-stat-container {
  width: 33%;
  margin: 5px 5px 0 5px;
  background-color: white;
  padding: 10px;
}

@media only screen and (max-width: 600px) {
  .comparison-stat-container {
    width: 100%;
    margin: 5px 0 0 0;
  }
}

@media only screen and (min-width: 900px) {
  .comparison-stat-container {
    width: 455px;
  }
}

.team-select-dropdown {
  width: 200px;
  color: #000000;
  text-align: left;
}

.record-text {
  font-size: 28px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.record-text-in-chart {
  position: absolute;
  padding: 75px 51px;
}

.radial-chart-tooltip {
  background-color: #282c34;
  color: #ffffff;
  padding: 10px;
  border-radius: 5px;
  border: 5px solid #282c34;
  text-align: left;
  min-width: 120px;
}

.radial-chart-tooltip-name {
  font-size: 18px;
}

.radial-chart-tooltip-value {
  font-size: 24px;
}

.stat-title-container {
  width: 100%;
}

.credits-and-info-container {
  background-color: #ffffff;
  border-top: 1px solid #555555;
  width: 100%;
  text-align: center;
  height: 2vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  min-height: 97vh;
}

.team-dashboard {
  display: grid;
  grid-template-columns: 25fr 25fr 25fr 25fr;
  /* grid-template-columns: 33fr 33fr 33fr; */
  grid-template-rows: 33fr 33fr 33fr;
  height: 97vh;
}

.team-info-pane-container {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  padding: 10px;
}

.team-info-pane {
  width: 100%;
  height: 100%;
  padding: 10px;
  text-align: left;
  background-color: #ffffff;
}

.team-title {
  width: 100%;
  text-align: left;
  font-size: 36px;
  font-family: 'Aldrich';
  font-style: italic;
  margin-bottom: 10px;
}

.team-info-component {
  margin: 10px 0;
}

.comparison-chart-container {
  /* background-color: red; */
  margin: 5px;
}

